.header {
  grid-area: header;
  background-color: white;
  border-radius: 5px;
  -webkit-box-shadow: 0px 0px 69px -16px rgba(34, 60, 80, 0.11);
          box-shadow: 0px 0px 69px -16px rgba(34, 60, 80, 0.11);
  display: -ms-grid;
  display: grid;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;

  position: relative;
}

.header img {
  margin-left: 30px;
  height: 30px;
  cursor: pointer;
}

.name {
  margin-left: 10px;
  margin-top: 10px;
  font-size: 30px;
  font-weight: 700;
  color: rgb(49, 148, 194);
}

.loginBlock {
  outline: none;
  border: 1px solid black;
  cursor: pointer;
  right: 10px;
  position: absolute;
  height: 30px;
  width: 100px;
  background-color: rgb(148, 148, 148);
  float: right;
  border-radius: 5px;
}

.loginBlock_hidden {
  right: 0px;
  position: absolute;
  height: 30px;
  width: 100px;
  float: right;
  visibility: hidden;
}
